import React from 'react';
import Kit from '../components/Kit/Kit';
import Layout from '../components/Layout/Layout';
import { graphql, useStaticQuery } from 'gatsby';

function KitPage() {
	const data = useStaticQuery(graphql`
		{
			allContentfulProduct(sort: { fields: updatedAt, order: DESC }, limit: 6) {
				edges {
					node {
						titlemain
						descmain
						price
						slug
						img {
							file {
								url
							}
						}
						rate
					}
				}
			}
		}
	`);
	return (
		<Layout title="کیت">
			<Kit products={data.allContentfulProduct.edges} />
		</Layout>
	);
}

export default KitPage;
