import React from 'react';
import { Rate } from 'antd';
import { Link } from 'gatsby';
import classes from './EachProduct.module.scss';

function EachProduct({ products }) {
	return (
		<div className={`container ${classes.cont}`}>
			{products.map((each) => {
				const slug = each.node.slug;
				const img = each.node.img[0].file.url;
				const rate = each.node.rate;
				const price = each.node.price.toLocaleString('ar-EG');
				const title = each.node.titlemain;
				return (
					<Link to={`/${slug}`} className={classes.link}>
						<div className={classes.contitem}>
							<div className={classes.contimg}>
								<img className={`img-fluid ${classes.img}`} alt="img" src={img} />
							</div>
							<p className={classes.titlemain}>{title}</p>
							<Rate className={classes.rate} allowHalf defaultValue={rate} disabled />
							<p className={classes.price}>{price} هزارتومان</p>
							<button className={classes.btn}>خرید</button>
						</div>
					</Link>
				);
			})}
		</div>
	);
}

export default EachProduct;
